export const ZERO_ADDRESS = '0x0000000000000000000000000000000000000000';
export const ETHAddress = ZERO_ADDRESS;
export const WethAddress = readNetwork.WETH;

export const defaultTokenInfo = {
  decimals: 18,
  name: '',
  symbol: '',
  address: ZERO_ADDRESS,
  logoURI: questionImg,
  value: ZERO_ADDRESS,
  label: '',
  labelText: '',
}

export const defaultETHInfo = {
  ...defaultTokenInfo,
  decimals: 18,
  name: 'ETH',
  symbol: 'ETH',
  address: ETHAddress,
  logoURI: 'https://s2.coinmarketcap.com/static/img/coins/64x64/1027.png',
  value: ETHAddress,
  label: 'ETH',
  labelText: 'ETH',
}

// const NormalTokensAddress = [
//   WethAddress,
//   ...(readNetwork.TOKENS|| []),
// ]

function formatElem(elem) {
  const labelText = `${elem.symbol}${elem.name !== elem.symbol ? '(' + elem.name + ')' : ''}`;

  const tmpTokenInfo = {
    ...elem,

    searchInfo: `${labelText}_${elem.address}`.toLowerCase(),
    value: `${elem.address}`.toLowerCase(),
    address: `${elem.address}`.toLowerCase(),
    label: labelText,
  };
  return tmpTokenInfo;
}

const tokensGlobal = [
  defaultETHInfo,
];
tokensGlobal[defaultETHInfo.address] = defaultETHInfo;
// console.log('tokensGlobal', tokensGlobal);
window.tokensGlobal = tokensGlobal;

const addToken = (arr, cb) => {
  // console.log('addToken arr', arr);
  _.map(arr, (token) => {
    if (!tokensGlobal[token.address]) {
      tokensGlobal.push(token);
      tokensGlobal[token.address] = token;
    }
  });
  cb({
    length: tokensGlobal.length,
    tokens: tokensGlobal,
  });
}

export function useTokens() {
  const [promiseInfo, setPromiseInfo] = useState({});
  // const { contracts } = useContext(UserContext);
  const [ tokensInfo, setTokensInfo] = useState({
    length: tokensGlobal.length,
    tokens: tokensGlobal,
  });
  const { signingProvider } = useContext(NetworkContext);


  useEffect(() => {
    axios
      .get(process.env.PUBLIC_URL + '/coinmarketcap-api-uniswap-all.json')
      .then((res) => {
        const tokensFormat = _.map(res.data.tokens, (elem) => {
          const token = formatElem(elem);
          // if (token.address === NETWORKS_BY_NAME.mainnet.WETH) {
          //   token.address = readNetwork.WETH;
          // }
          return token;
        });
        addToken(tokensFormat, (tokensInfo) => {
          setTokensInfo(tokensInfo);
        });
      })
      .catch((rej) => {
        console.log('tokens all info rej', rej);
      });
  }, [
    // addToken,
  ]);

  function getTokens(address) {
    const formatAddress = `${address}`.toLowerCase();
    // console.log('getTokens', formatAddress);
    if (!isAddress(formatAddress)) {
      return Promise.reject('is not Address');
    }

    // return from cache
    if (tokensGlobal[`${formatAddress}`.toLowerCase()]) {
      return Promise.resolve(tokensGlobal[`${formatAddress}`.toLowerCase()]);
    }

    // one address just promise once is success.
    if (promiseInfo[formatAddress]) {
      return promiseInfo[formatAddress];
    }

    const signerOrProvider = signingProvider?.getSigner() ?? readProvider;

    const contractBuildInfo = getContractBuildInfo();
    // window.contractBuildInfo = contractBuildInfo;

    const $FeatureReader = new Contract(contractBuildInfo.FeatureReader.address, contractBuildInfo.FeatureReader.abi, signerOrProvider);
    promiseInfo[formatAddress] = $FeatureReader.tokenDetail(formatAddress).then((res) => {
      // console.log('formatAddress res', res);
      window.res = res;
      const newToken = formatElem({
        ...defaultTokenInfo,
        name: res.name,
        symbol: res.symbol,
        decimals: res.decimals.toNumber(),
        address: formatAddress,
        logoURI: questionImg,
        noShowLogoURI: true,
      });
      const existTokenInfo = _.find(tokensGlobal, {
        symbol: newToken.symbol,
      });
      if (existTokenInfo && existTokenInfo.logoURI) {
        newToken.logoURI = existTokenInfo.logoURI;
      }

      if (!tokensGlobal[formatAddress]) {
        addToken([newToken], (tokensInfo) => {
          setTokensInfo(tokensInfo);
        });
      }
    }).catch((rej) => {
      // console.log('rej', rej);
      delete promiseInfo[formatAddress];
      setPromiseInfo(promiseInfo);
      return Promise.reject(rej);
    });
    setPromiseInfo(promiseInfo);
    return promiseInfo[formatAddress];
  }

  // const NormalTokens = useMemo(() => {
  //   if (!tokensInfo?.length) {
  //     return [];
  //   }

  //   const arr = [];
  //   const addressArr = [...NormalTokensAddress];
  //   if (contracts?.FeatureToken?.address) {
  //     addressArr.push(contracts?.FeatureToken?.address);
  //   }
  //   _.map(addressArr, (elem) => {
  //     const token = tokensGlobal[`${elem}`.toLowerCase()];
  //     if (token?.address) {
  //       arr.push(token);
  //     }
  //     else {
  //       getTokens(elem);
  //     }
  //   });

  //   return arr;
  // }, [
  //   tokensInfo?.length,
  //   contracts?.FeatureToken?.address,
  // ]);

  function TokenInfo({
    address,
    render,
  }) {
    const token = tokensGlobal[`${address}`.toLowerCase()];
    if (!token) {
      getTokens(address);
      return (<Loading />);
    }
    return (<React.Fragment>
      {render(token)}
    </React.Fragment>)
  }

  return {
    // NormalTokens,
    tokensInfo,
    getTokens,
    TokenInfo,
  }
}
