import { TokensContext } from 'contexts/tokensContext';
import { useTokens } from 'hooks/tokens';

export default function UserProvider({ children }) {
  const {
    tokensInfo,
    getTokens,
    TokenInfo,
    // NormalTokens,
  } = useTokens();
  window.tokensInfo = tokensInfo;

  return (
    <TokensContext.Provider
      value={{
        tokensInfo,
        getTokens,
        TokenInfo,
        // NormalTokens,
      }}
    >
      {children}
    </TokensContext.Provider>
  )
}
